import React from 'react';
import style from './orderInfo.module.css'

const OrderInfo = ({date, number}) => {
  return (
      <div className={style.info}>
        <ul>
          <li> </li>
          <li>  :{date}</li>
          <li>-:{number}</li>
        </ul>
      </div>
  );
};

export default OrderInfo;