import React from 'react'
import '../../../../../assets/styles/App.scss'
import CheckBox from '../../../../../fields/CheckBox';


export const Filter = () => {
  return (
      <div className='filter'>
        <div className="filter__block">
          <p className="filter__block__title"></p>
          <CheckBox label={''} id={'1'}/>
          <CheckBox label={''} id={'1'}/>
          <CheckBox label={''} id={'1'}/>
        </div>

        <div className="filter__block">
          <p className="filter__block__title"></p>
          <CheckBox label={'-'} id={'1'}/>
          <CheckBox label={'-'} id={'1'}/>
          <CheckBox label={' '} id={'1'}/>
          <CheckBox label={' '} id={'1'}/>
        </div>

        <div className="filter__block">
          <p className="filter__block__title"> </p>
          <CheckBox label={''} id={'1'}/>
          <CheckBox label={'  '} id={'1'}/>
          <CheckBox label={'  '} id={'1'}/>
          <CheckBox label={' '} id={'1'}/>
          <CheckBox label={''} id={'1'}/>
          <CheckBox label={'  '} id={'1'}/>
          <CheckBox label={''} id={'1'}/>
          <CheckBox label={''} id={'1'}/>
        </div>

        <div className="button button_black"></div>
      </div>
  );
}


