import React from 'react'
import {Card} from './Card';

export const List = ({title}) => {
  let data = [
      {title: '', image: 'card_image_1', disigner: ' ', price: '5999'},
      {title: '', image: 'card_image_2', disigner: ' ', price: '3999'},
      {title: '', image: 'card_image_3', disigner: ' ', price: '4999'},
      {title: '', image: 'card_image_4', disigner: ' ', price: '3999'},
      {title: '', image: 'card_image_5', disigner: ' ', price: '3999'},
      {title: '', image: 'card_image_6', disigner: ' ', price: '2999'},
      {title: '', image: 'card_image_1', disigner: ' ', price: '5999'},
      {title: '', image: 'card_image_2', disigner: ' ', price: '3999'},
      {title: '', image: 'card_image_3', disigner: ' ', price: '4999'},
      {title: '', image: 'card_image_4', disigner: ' ', price: '3999'},
      {title: '', image: 'card_image_5', disigner: ' ', price: '3999'},
      {title: '', image: 'card_image_6', disigner: ' ', price: '2999'},
      ]
  return (
      <div className='product_list__items'>
        <h1 className='product_list__items__title'>{title}</h1>
        <div className="product_list__items__wrapper">
          {data.map((val, key)=> {
            return <Card key={Date.now() + val.title + key} data={val}/>
          })}
        </div>
      </div>
  )
}