import React from 'react';
import style from './mainContent.module.css';
import Cart from '../../HOC/cart/cart';
import StatusCart from './statusCart/statusCart';
import CartContent from './cartContent/cartContent';
import SimpleRating from './ratingStar/ratingStar';

const rows = [
  {title: '', value: 1},
  {title: ' ', value: 0},
  {title: '', value: 5},
  {title: '', value: 2},
];

const rows2 = [
  {title: ' ', value: 15222},
  {title: ' ', value: 0},
  {title: '', value: 0},
  {title: '  ', value: 15222},
];

const rows3 = [
  {title: ' ', value: 10},
  {title: ' ', value: 5},
  {title: '', value: 1},
];

const MainContent = () => {
  return (
      <div>
        <div className={style.title}><h2></h2></div>
        <div className={style.content}>
          <div className={style.cartPad}>
          <Cart title={''}>
            <StatusCart/>
          </Cart>
        </div>
          <div className={style.cartPad}>
            <Cart title={'  '}>
              <CartContent
                  rows={rows}
              />
            </Cart>
          </div>
          <div className={style.cartPad}>
            <Cart title={''}>
              <div className={style.rating}>
                <span> </span>
                <SimpleRating/>
              </div>
              <div className={style.rating}>
                <span> </span>
                <div>12</div>
              </div>
            </Cart>
          </div>
          <div className={style.cartPad}>
          <Cart title={' '}>
            <CartContent
                rows={rows3}
            />
          </Cart>
          </div>
          <div className={style.cartPad}>
            <Cart title={''}>
              <CartContent
                  rows={rows2}
              />
            </Cart>
          </div>
        </div>
      </div>
  );
};

export default MainContent;