import React from 'react';
import style from './company.module.css'
import Input from '../../UI/input/input';
import Button from '../../UI/button/button';

const inputs = [
  {label: '', id: '1'},
  {label: ' ', id: '2'},
  {label: ' ', id: '3'},
  {label: ' ', id: '4'},
  {label: ' ', id: '5'},
  {label: ' ', id: '6'},
  {label: ' ', id: '6'},
]

const Company = () => {

  const renderInputs = () => {
    return inputs.map((item, index) => {
      return(
          <Input
            label={item.label}
            id={item.id}
            key={index}
          />
      )
    })
  }

  return (
      <div className={style.company}>
        <div className={style.photoInfo}>
          <img src={require('../../../../assets/img/lk_plus_data.svg')}/>
          <div>
            <span className={style.title}> </span>
            <ul>
              <li> - JPEG, PNG</li>
              <li> -  700  1400 </li>
              <li> -   10 </li>
            </ul>
          </div>
        </div>
        <form>
        {renderInputs()}
        <div className={style.mini}>
            <Input id={'7'} label={''} width={'177px'}/>
            <Input id={'8'} label={''} width={'177px'}/>
        </div>
        <div className={style.btn}>
          <Button
            width={'145px'}
            height={'48'}
            title={''}
            background={'#CDCDCD'}
          />
        </div>
        </form>
      </div>
  );
};

export default Company;